// src/features/auth/context/auth-context.jsx
"use client"

import React from "react"

const AuthContext = React.createContext(undefined)
AuthContext.displayName = "AuthContext"

async function parseJson(response) {
  try {
    return await response.json()
  } catch (error) {
    return null
  }
}

export function AuthProvider({ children, initialUser = null }) {
  const [user, setUser] = React.useState(initialUser)

  const login = React.useCallback(async (options = {}) => {
    const response = await fetch("/api/auth/sso-login", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      credentials: "include",
      body: JSON.stringify(options ?? {}),
    })

    if (!response.ok) {
      const data = await parseJson(response)
      const message = data?.message ?? "SSO login failed"
      throw new Error(message)
    }

    const data = await parseJson(response)
    const nextUser = data?.user ?? null
    setUser(nextUser)
    return { user: nextUser, data }
  }, [])

  const logout = React.useCallback(async () => {
    const response = await fetch("/api/auth/logout", {
      method: "POST",
      credentials: "include",
    })

    if (!response.ok) {
      const data = await parseJson(response)
      const message = data?.message ?? "Logout failed"
      throw new Error(message)
    }

    setUser(null)
    return true
  }, [])

  const refresh = React.useCallback(async () => {
    const response = await fetch("/api/auth/session", {
      method: "GET",
      credentials: "include",
    })

    if (!response.ok) {
      setUser(null)
      return null
    }

    const data = await parseJson(response)
    const nextUser = data?.user ?? null
    setUser(nextUser)
    return nextUser
  }, [])

  const value = React.useMemo(
    () => ({
      user,
      isAuthenticated: Boolean(user),
      login,
      logout,
      refresh,
      setUser,
    }),
    [user, login, logout, refresh]
  )

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>
}

export function useAuth() {
  const context = React.useContext(AuthContext)
  if (!context) {
    throw new Error("useAuth must be used within an AuthProvider")
  }
  return context
}
